<template>
  <div class="itemBox">
    <div>
      <p class="title">商品</p>
      <el-form ref="form" :model="itemData" size="mini">
        <el-form-item label="添加商品" class="navigation">
          <div class="goodsUpload" @click="commodityShow = true">+</div>
          <ul class="goodsUploadList" v-show="itemData.goodsData.length!=0">
            <li
              v-for="(item,index) in itemData.goodsData"
              :key="index"
              @mouseover="mouseOver(index)"
              @mouseleave="mouseLeave"
            >
              <img :src="item.imageUrl" alt />
              <img
                class="delBtn"
                :class="hoverIndex==index?'delBtnShow':''"
                src="@/assets/decorateBox/goods/close.png"
                @click="delGoods(index)"
                alt
              />
            </li>
          </ul>
        </el-form-item>

        <el-form-item label="列表样式" class="navigation">
          <el-radio-group v-model="itemData.listType" @change="listSelect">
            <el-radio :label="0">大图样式</el-radio>
            <el-radio :label="1">小图样式</el-radio>
            <el-radio :label="2">一行三个</el-radio>
            <el-radio :label="3">详细列表</el-radio>
            <el-radio :label="4">横向滑动</el-radio>
            <el-radio :label="5">一大两小</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="商品样式" class="showType">
          <el-radio-group v-model="itemData.chamfering" @change="showType(0)">
            <span class="showClass">卡片倒角</span>
            <el-radio :label="0">直角</el-radio>
            <el-radio :label="1">圆角</el-radio>
          </el-radio-group>
          <el-radio-group v-model="itemData.projection" @change="showType(1)">
            <span class="showClass">卡片投影</span>
            <el-radio :label="0">无投影</el-radio>
            <el-radio :label="1">有投影</el-radio>
          </el-radio-group>
          <el-radio-group v-model="itemData.fill" @change="showType(2)">
            <span class="showClass">图片填充</span>
            <el-radio :label="0">填充</el-radio>
            <el-radio :label="1">周边留白</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="显示内容">
          <el-checkbox v-model="itemData.showGoodsName">商品名称</el-checkbox>
          <el-checkbox v-model="itemData.showGoodsPrice">商品价格</el-checkbox>
          <el-checkbox v-model="itemData.showBuyBtn" @change="changeShow(0)">购买按钮</el-checkbox>

          <el-radio-group v-model="itemData.buyBtn" class="buyStyle" @change="selsecBtn">
            <el-radio :label="0" :disabled="radioType">样式1</el-radio>
            <el-radio :label="1" :disabled="radioType">样式2</el-radio>
            <el-radio :label="2" :disabled="radioType||selectStyle">样式3</el-radio>
            <el-radio :label="3" :disabled="radioType||selectStyle">样式4</el-radio>
            <el-radio :label="4" :disabled="radioType">样式5</el-radio>
            <el-radio :label="5" :disabled="radioType">样式6</el-radio>
            <el-radio :label="6" :disabled="radioType||selectStyle">样式7</el-radio>
            <el-radio :label="7" :disabled="radioType||selectStyle">样式8</el-radio>
          </el-radio-group>
          <el-input
            v-show="rightNowbtn"
            v-model="itemData.buyBtnName"
            @blur="btnPd"
            maxlength="4"
            placeholder="请输入按钮名称"
            :class="btnPdClass==true?'btnPdClass':''"
          ></el-input>
          <el-input
            v-show="rightAwaybtn"
            v-model="itemData.buyBtnName1"
            @blur="btnPd1"
            maxlength="4"
            placeholder="请输入按钮名称"
            :class="btnPdClass1==true?'btnPdClass':''"
          ></el-input>
          <el-checkbox v-model="itemData.showCornerMark" @change="changeShow(1)">商品角标</el-checkbox>
          <el-radio-group v-model="itemData.cornerMark" class="buyStyle">
            <el-radio :label="0" :disabled="radioType1">新品</el-radio>
            <el-radio :label="1" :disabled="radioType1">热卖</el-radio>
            <el-radio :label="2" :disabled="radioType1">NEW</el-radio>
            <el-radio :label="3" :disabled="radioType1">HOT</el-radio>
            <el-radio :label="4" :disabled="radioType1">自定义</el-radio>
            <div style="display:flex">
              <div
                class="customImg1"
                @mouseover="mouseOver1"
                @mouseleave="mouseLeave1"
                v-show="Object.keys(itemData.anglePicture).length!=0&&itemData.cornerMark==4"
              >
                <img :src="itemData.anglePicture.imageUrl" alt />
                <img
                  class="delBtn"
                  :class="hoverIndex1==1?'delBtnShow':''"
                  src="@/assets/decorateBox/goods/close.png"
                  @click="delBgs1"
                  alt
                />
              </div>
              <div class="customImg" v-show="itemData.cornerMark==4" @click="addBanner1">
                <div class="goodsUpload">+</div>
                <div class="tips">推荐使用 36x36 像素的 .png 图片</div>
              </div>
            </div>
          </el-radio-group>
        </el-form-item>
      </el-form>
    </div>
    <!-- 商品组件 -->
    <normalCommodity 
      :commodityIds="itemData.commodityIds.join(',')" 
      :commodityShow="commodityShow"
      @commodityClose="commodityClose"
      @commodityFun="commodityFun"
      ></normalCommodity>
    <!-- 图片库 -->
    <el-dialog
      title="图库列表"
      :visible.sync="dialogVisible1"
      width="76%"
      :close-on-click-modal="false"
      @close="submitFormqx1"
      :append-to-body="true"
      class="add-dialog"
      top="6vh">
      <imageMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="submitFormqx1"
        @subMit="submitForm1"
      ></imageMaterial>
    </el-dialog>
  </div>
</template>

<script>
import imageMaterial from "@/components/shopMaterial/imageMaterial";
import normalCommodity from "@/components/popover/normalCommodity";
export default {
  props: {
    //父级传递的数据
    itemData: {
      type: Object
    }
  },
  data() {
    return {
      btnPdClass: false,
      btnPdClass1: false,
      hoverIndex: -1, //hover的index
      loadingbut: false, //审核提交加载中
      loadingbuttext: "确 定",
      pageNumber: 5, //默认分页条数
      nowPage: 1, //默认第几页
      total: 1,
      tableData: [],
      selectData: [],
      search: {
        name: "",
        shopCategoryId: ""
      },
      search1: {
        name: "",
        shopCategoryId: ""
      },
      multipleSelection: [], //选中的数据

      loading: false,
      dialogVisible: false,
      radioType: false,
      radioType1: false,
      selectStyle: false,
      rightAwaybtn: false, //马上抢
      rightNowbtn: false, //立即购买
      dialogVisible1: false,
      hoverIndex1: -1, //hover的index
      shopId: "",
      getPost: true,

      commodityShow:false,
    };
  },
  created() {
    this.shopId = Number(localStorage.getItem("shopId"));
  },
  mounted() {
    if (this.itemData.buyBtn == 3 || this.itemData.buyBtn == 7) {
      this.rightNowbtn = true;
    }
    if (this.itemData.buyBtn == 2 || this.itemData.buyBtn == 6) {
      this.rightAwaybtn = true;
    }
    console.log(this.itemData.buyBtn);
    // 回顯顯示內容
    if (this.itemData.showBuyBtn == false) {
      this.radioType = true;
    } else {
      this.radioType = false;
    }

    if (this.itemData.showCornerMark == false) {
      this.radioType1 = true;
    } else {
      this.radioType1 = false;
    }
    // 回顯列表樣式
    if (this.itemData.listType == 2) {
      this.selectStyle = true;
    } else if (this.itemData.listType == 4) {
      this.selectStyle = true;
    } else {
      this.selectStyle = false;
    }
  },
  components: {
    imageMaterial,
    normalCommodity
  },
  methods: {
    // 商品列表弹出框
    commodityClose(){
      this.commodityShow = false;
    },
    commodityFun(e){
      e.map(item => {
        this.itemData.goodsData.push(item);
        this.itemData.commodityIds.push(item.productId);
      });
      this.commodityShow = false;
    },
    //展示样式
    showType(e) {
      switch (e) {
        case 0:
          if (this.itemData.chamfering == 0) {
            this.itemData.chamferingStyle = 0;
          } else {
            this.itemData.chamferingStyle = "8px";
          }
          break;
        case 1:
          if (this.itemData.projection == 0) {
            this.itemData.projectionStyle = "0px 0px 0px 0px";
          } else {
            this.itemData.projectionStyle =
              "0px 2px 2px 0px rgba(153,153,153,0.1)";
          }
          break;
        case 2:
          if (this.itemData.fill == 0) {
            this.itemData.fillStyle = 0;
          } else {
            this.itemData.fillStyle = "5px";
          }
          break;
      }
    },
    //多选框选中事件
    changeShow(e) {
      //当购买按钮被选中
      if (e == 0) {
        if (this.itemData.showBuyBtn == false) {
          this.radioType = true;
          this.itemData.buyBtn = -1;
          this.rightAwaybtn = false;
          this.rightNowbtn = false;
        } else {
          this.radioType = false;
          this.itemData.buyBtn = 0;
        }
      } else {
        //当商品角标被选中
        if (this.itemData.showCornerMark == false) {
          this.radioType1 = true;
          this.itemData.cornerMark = -1;
        } else {
          this.radioType1 = false;
          this.itemData.cornerMark = 0;
        }
      }
    },
    //列表样式变化
    listSelect() {
      if (this.itemData.listType == 2) {
        this.selectStyle = true;
        this.rightAwaybtn = false;
        this.rightNowbtn = false;

        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 1;
        } else {
          this.itemData.buyBtn = -1;
        }
      } else if (this.itemData.listType == 4) {
        this.selectStyle = true;
        this.rightAwaybtn = false;
        this.rightNowbtn = false;
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 1;
        } else {
          this.itemData.buyBtn = -1;
        }
      } else {
        this.selectStyle = false;
      }
      if (this.itemData.listType == 0) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 7;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 1) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 4;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 3) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 2;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
      if (this.itemData.listType == 5) {
        //判断是否按钮框选中没
        if (this.itemData.showBuyBtn == true) {
          this.itemData.buyBtn = 6;
        } else {
          this.itemData.buyBtn = -1;
        }
      }
    },
    //单选框变化
    selsecBtn() {
      // 如果buyBtn=2，6 是马上抢 buyBtnName1
      // 如果buyBtn=3，7 是立即购买 buyBtnName
      if (this.itemData.buyBtn == 2 || this.itemData.buyBtn == 6) {
        this.rightAwaybtn = true;
        this.rightNowbtn = false;
      } else if (this.itemData.buyBtn == 3 || this.itemData.buyBtn == 7) {
        this.rightNowbtn = true;
        this.rightAwaybtn = false;
      } else {
        this.rightAwaybtn = false;
        this.rightNowbtn = false;
      }
    },
    //弹窗取消按钮
    submitFormqx() {
      this.dialogVisible = false;
      this.$refs.search1.resetFields();
      this.search.name = "";
      this.search.shopCategoryId = "";
    },
    // 选中的
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    closed() {
      this.loadingbut = false;
      this.loadingbuttext = "确 定";
    },
    
    // 移入事件
    mouseOver(index) {
      this.hoverIndex = index;
    },
    // 移出事件
    mouseLeave() {
      this.hoverIndex = -1;
    },
    // 删除商品
    delGoods(index) {
      this.itemData.goodsData.splice(index, 1);
      this.itemData.commodityIds.splice(index, 1);
    },

    /**图库列表**/

    // 弹窗并选择商品
    addBanner1() {
      this.dialogVisible1 = true;
      this.getPost = true;
    },
    //弹窗取消按钮
    submitFormqx1() {
      this.dialogVisible1 = false;
    },
    //弹窗确定按钮
    submitForm1(e) {
      console.log(e.length);
      if (e.length != 0) {
        this.itemData.anglePicture = e[0];
        this.dialogVisible1 = false;
      } else {
        this.$message.error("请选择图片");
      }
      this.getPost = false;
    },

    // 移入事件
    mouseOver1() {
      this.hoverIndex1 = 1;
    },
    // 移出事件
    mouseLeave1() {
      this.hoverIndex1 = -1;
    },
    // 删除角标
    delBgs1(index) {
      this.itemData.anglePicture = {};
    },
    isChn(str) {
      if (!/^[\u4E00-\u9FA5]{2,4}$/.test(str)) {
        // alert("不全是中文");
        return false;
      } else {
        // alert("全是中文");
        return true;
      }
    },
    // 按钮输入框判断
    btnPd() {
      // console.log(this.itemData.buyBtnName.length);
      // console.log(this.isChn(this.itemData.buyBtnName));
      if (this.isChn(this.itemData.buyBtnName) == false) {
        this.$message.error("只能输入2到4个汉字");
        this.btnPdClass = true;
      } else {
        this.btnPdClass = false;
      }
    },
    btnPd1() {
      if (this.isChn(this.itemData.buyBtnName1) == false) {
        this.$message.error("只能输入2到4个汉字");
        this.btnPdClass1 = true;
      } else {
        this.btnPdClass1 = false;
      }
    }
  }
};
</script>
<style>
.add-dialog .el-dialog__body {
  padding: 0px 20px 50px 20px;
}
</style>
<style scoped lang="less">
.active {
  border: 1px solid #000;
}
.title {
  padding: 20px 18px 24px 18px;
  border-bottom: 1px solid #eee;
  color: #333;
  font-size: 16px;
  font-weight: 700;
}
.el-dialog__body {
  .pagination {
    padding-top: 20px;
  }
  .search {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    .el-form-item {
      margin-bottom: 0px;
      margin-right: 20px;
    }
    .formBtnBox {
      margin-left: 20px;
    }
  }
  img {
    width: 100px;
    margin: 0 auto;
  }

  .bannerData {
    display: flex;
    flex-wrap: wrap;
    li {
      // width: 280px;
      box-sizing: border-box;
      padding: 6px;
      background-color: #f6f6f6;
      margin-right: 10px;
      margin-bottom: 20px;
      border: 1px solid #f6f6f6;
      cursor: pointer;
      transition: 0.2s;
      &:hover {
        background-color: #ecf5ff;
        border: 1px solid #a2cfff;
      }

      img {
        width: 260px;
        height: 166px;
        margin-bottom: 8px;
      }
      .sizes {
        color: #999;
      }
    }
    .clickLi {
      background-color: #ecf5ff;
      border: 1px solid rgba(0, 121, 253, 1);
    }
  }
}

.itemBox /deep/ .el-form {
  margin: 18px 24px;
  .el-form-item__label {
    display: block;
    float: inherit;
    text-align: inherit;
    padding: 0;
    color: #222;
    font-weight: 100;
    margin-bottom: 12px;
  }
  .showType {
    border-bottom: 1px solid #eee;
    padding-bottom: 24px;
    .el-radio {
      width: 88px;
      margin: 0;
    }
    .showClass {
      display: inline-block;
      width: 52px;
      height: 24px;
      color: #999999;
      font-size: 12px;
      text-align: center;
      margin-right: 24px;
    }
  }
  .el-input__inner {
    height: 34px;
    border-radius: 2px;
    width: 126px;
    margin: 6px 0 16px 23px;
  }
  .el-form-item__error {
    padding-top: 5px;
  }
  .el-radio__label {
    font-size: 12px;
    // color: #333;
  }
  .navigation {
    border-bottom: 1px solid #eee;
    padding-bottom: 16px;
    .el-radio {
      margin-left: 6px;
      width: 60px;
      margin-bottom: 10px;
    }
  }
  .custom {
    display: flex;
    align-items: center;
    .el-color-picker {
      margin-left: 16px;
    }
  }
  .el-checkbox-group {
    margin-left: 10px;
  }
  .el-checkbox {
    display: block;
    margin: 0;
  }
  .goodsUploadList {
    display: flex;
    flex-wrap: wrap;
    li {
      width: 68px;
      height: 68px;
      transition: 0.2s;
      cursor: pointer;
      border: 1px solid #ddd;
      box-sizing: border-box;
      padding: 4px;
      position: relative;
      margin-right: 5px;
      margin-bottom: 10px;
      img {
        width: 100%;
        height: 100%;
        vertical-align: top;
      }
      .delBtn {
        width: 16px;
        height: 16px;
        position: absolute;
        right: -8px;
        top: -8px;
        display: none;
      }
      .delBtnShow {
        display: block;
      }
    }
    li:hover {
      border: 1px solid #6babec;
    }
  }
  .goodsUpload {
    font-size: 34px;
    background: #fcfcfc;
    font-weight: 100;
    color: #9a9a9a;
    width: 68px;
    height: 68px;
    border-radius: 4px;
    text-align: center;
    line-height: 68px;
    border: 1px dashed #ddd;
    margin-bottom: 10px;
    transition: 0.2s;
    cursor: pointer;
  }
  .goodsUpload:hover {
    background-color: #ecf5ff;
    border: 1px dashed rgba(0, 121, 253, 1);
  }

  .buyStyle {
    display: flex;
    flex-wrap: wrap;
    .el-radio {
      width: 44px;
      margin: 6px 0 5px 25px;
    }
    .goodsUpload {
      margin: 7px 10px 0 27px;
    }
  }
  .customImg1 {
    width: 68px;
    height: 68px;
    transition: 0.2s;
    cursor: pointer;
    border: 1px solid #ddd;
    box-sizing: border-box;
    padding: 4px;
    position: relative;
    margin: 8px -14px 0 27px;
    img {
      width: 100%;
      height: 100%;
    }
    .delBtn {
      width: 16px;
      height: 16px;
      display: none;
      position: absolute;
      right: -8px;
      top: -8px;
      padding: 0;
    }
    .delBtnShow {
      display: block;
    }
    &:hover {
      border: 1px solid #6babec;
    }
  }
  .customImg {
    display: flex;
    align-items: center;
    cursor: pointer;
    .tips {
      font-size: 14px;
      width: 128px;
      line-height: 20px;
      color: #409eff;
    }
  }
}
.btnPdClass /deep/ .el-input__inner {
  border-color: #f56c6c;
}
</style>
